<template>
  <div class="conf-audit">
      <div class="error">
        <img src="../assets/images/conf-error.png" alt="">
        <p><span></span> 
            </p>
        <button @click="go"></button>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            status: false
        };
    },
    created () {
    },
    methods: {
        go () {
            this.$router.push({name: 'index'});
        }
    }
};
</script>
<style lang="scss" scoped>
  .conf-audit .error{
    button {
      height: 2rem;
    }
  }
</style>



