<template>
    <section class="certificate">
      <div class="certificateTop">
        <p class="title">
          <span v-if="type === '0'"></span>
          <span v-else></span>
        </p>
        <img src="../assets/images/certificate-1_03.png" alt="">
        <p></p>
        {{licenseCode}}
      </div>
      <div class="certificateForm takeTheir">
              <div class="item row">
                 :<span>{{form.paperCreateTime}}</span>
              </div>
              <div class="item row">
                 :<span>{{type === '0' ? '' : ''}}</span>
              </div>
              <div class="item row">
                 :<span>{{form.obtainType | obtainType}}</span>
              </div>
              <div v-if="type === '0'" class="item row" >
                 :<span>{{form.recipientAddress}}</span>
              </div>
              <div class="item row" >
                 :<span v-if="form.obtainNum">{{form.obtainNum}}</span><span v-else>5</span>
              </div>
              <!-- <div class="item row">
                 :<span>>></span>
              </div> -->
      </div>
      <button class="submit-btn-1" @click="jump"></button>
    </section>
</template>

<script>
export default {
    data () {
        return {
            licenseCode: '',
            form: {},
            type: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            this.type = this.$route.query.type;
            // 
            if (this.type) {
                this.licenseCode = this.$route.query.licenseCode;
            } else {
                let pfa = localStorage.getItem('licenseCode');
                pfa && (this.licenseCode = pfa);
            }
            this.$http.post(this.$api.passport.queryLicensePaperByCode, {licenseCode: this.licenseCode}).then(res => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                }
            });
        },
        jump () {
            this.$router.push({name: 'index'});
        }
    }
};
</script>
<style scoped lang="scss">
</style>



