<template>
    <section class="license-paper">
        <div class="form">
          <span class="item row">
            <div class="col-sm col-sm-5 l"></div>
            <div class="col-sm col-sm-7 r">
              <span></span>
            </div>
          </span>
          <div class="item row">
            <div class="col-sm col-sm-4 l"></div>
            <div class="r">
              <input class="r" type="text" placeholder="" v-model="form.name">
            </div>
          </div>
          <div class="item row">
            <div class="col-sm col-sm-4 l"></div>
            <div class="col-sm col-sm-8 r">
              <input class="r" type="tel" placeholder="" v-model="form.mobile">
            </div>
          </div>
          <span class="item row">
            <div class="col-sm col-sm-6 l"></div>
            <br>
            <div class="address-wrap">
              <div></div>
              <div></div>
              <div @click="open()">{{areaName}}</div>
            </div>
          </span>
          <textarea class="textarea" cols="30" rows="10" v-model="addressStr" placeholder=""></textarea>
        </div>
        <!--  -->
        <div class="lc-tips">
          <p><i class="iconfont icon-jay"></i><span class="text"></span></p>
          <p>1. </p>
          <p>2. 20</p>
          <p>3. 6</p>
          <p>4. </p>
        </div>
        <div class="button-wrap">
            <button class="submit-btn" @click="send"> </button>
        </div>
        <!--  -->
        <span class="look" @click="look"></span>
        <mt-popup v-model="visible" position="bottom" class="region-popup">
          <mt-picker :slots="area" valueKey="name" @change="areaChange" :itemHeight="40"></mt-picker>
        </mt-popup>
    </section>
</template>

<script>
export default {
    data () {
        return {
            list: [],
            form: {
                address: null,
                mobile: null,
                name: null,
                passport: null,
                redirectUrl: ''
            },
            visible: false,
            area: [
                {
                    flex: 1,
                    values: [],
                    className: 'slot1',
                    textAlign: 'center'
                }
            ],
            areaID: null,
            areaName: '',
            urlStyle: null,
            addressStr: null
        };
    },
    created () {
        let time = new Date().getTime();
        this.form.redirectUrl = process.env.API_ROOT_MAIN.split('/html5')[0] + '/webSite/license4pay/success/' + time;
        // this.form.redirectUrl = 'http://' + location.host + '/webSite/license4pay/success/' + time;
    },
    mounted () {
        this.urlStyle = this.$route.query.urlStyle;
        let pfa = localStorage.getItem('licenseCode');
        pfa && (this.form.passport = pfa);
        // 
        if (this.$route.query.entrance === 'my') {
            this.form.passport = this.$route.query.licenseCode;
        }
    },
    methods: {
        areaChange (picker, values) {
            if (picker.getSlotValue(0)) {
                this.areaName = values[0].name;
            }
        },
        open () {
            // 
            this.$http.post(this.$api.passport.findByParent, {parent: 155387}).then(res => {
                if (res.data.code === 0) {
                    this.area[0].values = res.data.data;
                    this.visible = true;
                }
            });
        },
        look () {
            this.$router.push({name: 'proof', query: {urlStyle: this.urlStyle}});
        },
        send () {
            if (!this.check()) return;
            this.form.address = '' + this.areaName + this.addressStr;
            this.$http.post(this.$api.passport.savePassportExpress, this.form).then(res => {
                if (res.data.code === 0) {
                    this.addressStr = null;
                    location.href = res.data.data.payUrl;
                }
            });
        },
        check () {
            if (this.form.name === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!(/^(0|86|17951)?(13[0-9]|15[0-9]|166|17[3678]|18[0-9]|19[0-9]|14[57])[0-9]{8}$/.test(this.form.mobile))) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.areaName === '') {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.addressStr === null && this.addressStr.length === 0) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            return true;
        }
    }
};
</script>
<style scoped lang="scss">
  .textarea {
    padding: 0.7rem 0.625rem;
  }
  .region-popup {
    width: 100%;
  }
  .address-wrap {
    margin-top: px2rem(10);
    width: 100%;
    display: flex;
    div {
      flex: 1;
    }
  }
  input {
    border: none;
  }
  .lc-tips {
    .text {
      display: inline-block;
      vertical-align: middle;
    }
  }
</style>



